import { Component } from "react";

import { isMobile } from "../../utils/browser";

const isPortrait = () => {
  return window.innerHeight > window.innerWidth;
};

const isLandscape = () => {
  return window.orientation === 90 || window.orientation === -90;
};

export class ViewportHeight extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { isMobile: prevIsMobile } = prevState;

    if (prevIsMobile === null && typeof window === "object") {
      return {
        isMobile: isMobile(),
        width: window.innerWidth,
        height: window.innerHeight,
      };
    }

    return null;
  }

  state = {
    isMobile: null,
    width: null,
    height: null,
  };

  componentDidMount() {
    const { isMobile } = this.state;

    if (isMobile) {
      window.addEventListener("orientationchange", this.onResize);
    } else {
      window.addEventListener("resize", this.onResize);
    }

    this.onResize();
  }

  componentWillUnmount() {
    const { isMobile } = this.state;

    if (isMobile) {
      window.removeEventListener("orientationchange", this.onResize);
    } else {
      window.removeEventListener("resize", this.onResize);
    }
  }

  onResize = () => {
    const { isMobile, width, height } = this.state;

    if (isMobile) {
      console.info("-->******************************");
      console.info("--> width ggwp", width);
      console.info("--> height ggwp", height);
      console.info("--> window innerWidth ggwp", window.innerWidth);
      console.info("--> window innerHeight ggwp", window.innerHeight);
      console.info("-->******************************");
      document.documentElement.style.setProperty("--vh", `${window.innerHeight * 0.01}px`);
    } else {
      const vh = window.innerHeight * 0.01;

      document.documentElement.style.setProperty("--vh", `${vh}px`);
    }
  };

  render() {
    return null;
  }
}
